import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllLearningAndTrainingComponent } from './training-sessions/all-learning-and-training/all-learning-and-training.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
  {
    path: "all-learning-and-training",
    component: AllLearningAndTrainingComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LearningAndTrainingRoutingModule { }
